.tarefas{
    margin-top: 30px;

}
.tarefas li{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    transition: all 600ms;
}
.tarefas li:hover{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background-color: #52bedc;
    
}

.tarefas li .delete{
    margin-right: 15px;
    color: #f04c64;
}
.tarefas li .edit{
    margin-right: 15px;
    color: #51c5de;
}

.tarefas li:hover .delete,
.tarefas li:hover .edit{
    color: #000;

}

.tarefas li .delete,
.tarefas li .edit{
    transition: all 600ms;
    cursor: pointer;
}

.tarefas li span {
    display: flex;
    align-items: center;
}